<template>
    <div>
        <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
        >
            <div>
                <div style="position: fixed;width: 26rem;z-index: 999999">
                    <van-nav-bar
                            :title="'搜索结果：'+vod_name"
                            left-text="返回"
                            left-arrow
                            @click-left="onClickLeft"
                    />
                </div>
                <div style="padding-top: 4rem;margin-left: 0.6rem">
                    <van-empty v-if="list.length==0" description="暂无数据"/>
                    <div class="neiDiv" v-if="list.length!=0">
                        <van-row v-for="(item,index) in list" v-bind:key="item.vodId">
                            <div v-if="index<list.length&&index%2==0">
                                <van-col span="12">
                                    <div class="myimgSpanDiv" @click="video(list[index+1])">
                                        <van-image lazy-load class="my_img" :src="list[index].vodPic"
                                                   alt=""></van-image>
                                        <br>
                                        <span>{{list[index].vodName}}</span>
                                    </div>
                                </van-col>
                                <van-col span="12" v-if="index<list.length-1">
                                    <div class="myimgSpanDiv" @click="video(list[index+1])">
                                        <van-image lazy-load class="my_img" :src="list[index+1].vodPic"
                                                   alt=""></van-image>
                                        <br>
                                        <span>{{list[index+1].vodName}}</span>
                                    </div>
                                </van-col>
                            </div>
                        </van-row>
                    </div>
                </div>
            </div>
            <van-pagination v-model="currentPage" :total-items="items" :items-per-page="10" @change="changePage()" style="margin-bottom: 1rem" />
        </van-pull-refresh>
    </div>
</template>
<style>
    .neiDiv {
        margin-left: 1.8rem
    }
    .neiDiv span {
        display: -webkit-box; /** 将对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp: 1; /** 显示的行数，显示高度设置成行高，多余部分隐藏 **/
        overflow: hidden; /**超出部分隐藏 **/
        width: 9rem;
    }

    .myimgSpanDiv {
        width: 9rem;
    }

    .van-row {
        margin-bottom: 0.5rem;
    }

    .my_img {
        width: 9rem;
        height: 11rem;
    }

    .my_img img {
        border-radius: 5px;
    }
</style>
<script>
    import $ from "jquery";
    export default {
        data() {
            return {
                vod_name:'',
                isLoading:false,
                list: [],
                currentPage: 1,
                items:1,
            }
        },
        created() {
            this.vod_name = this.$route.query.vod_name;
            this.getData(1)
        },
        methods: {
            getData(pageNo){
                let _this=this;
                const a=_this.$toast({type:'loading',message:'加载中...',forbidClick:true});
                $.ajax({
                    url: _this.baseUrl + "/user/search",
                    data: {vod_name:_this.vod_name,pageNo:pageNo},
                    type: 'post',
                    traditional: true,
                    async: true,
                    dataType: "json",
                    success: function (data) {
                        _this.list=data.data.records;
                        _this.items=data.data.total;
                        _this.isLoading=false;
                        a.clear();
                    }
                });
            },
            onClickLeft() {
                this.$router.push({path: '/home'});
            },
            onRefresh() {
                let _this=this;
                setTimeout(function () {
                    _this.getData()
                },2000)

            },
            changePage(){
                let _this=this;
                _this.getData(_this.currentPage);
                document.body.scrollTop = 0;
                document.documentElement.scrollTop = 0;
            },
            video(item){
                this.$router.push({path: '/video', query: {vod_id: item.vodId, vod_name: item.vodName}});
            }
        },
    };
</script>
